<template>
    <el-date-picker
        v-model="times"
        :type="type"
        :formatType="formatType"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :defaultTime="['00:00:00','23:59:59']"
        :picker-options="pickerOptions"
        @change="getTimes"
      >
    </el-date-picker>
</template>
<script>
import { parseTime } from '../../utils/index'
export default {
    data(){
        return{
            times: '',
            startTime: '',
            endTime: '',
            pickerOptions: { // 时间范围选择不能大于一个月
                onPick: ({ maxDate, minDate }) => {
                    this.choiceDate = minDate.getTime()
                    if (maxDate) {
                    this.choiceDate = ''
                    }
                },
                disabledDate: (time) => {
                    if (this.choiceDate !== '') {
                        const one = 30 * 24 * 3600 * 1000
                        const minTime = this.choiceDate - one
                        const maxTime = this.choiceDate + one
                        return time.getTime() < minTime || time.getTime() > maxTime
                    }
                    return false
                }
            }
        }
    },
    props: {
        type:  {
            type: String,
            required: true,
        },
        formatType: {
            type: Boolean,
            required: false,
            default: true
        }
    },
    methods: {
        getTimes() {
            console.log(this.times)
            if(this.type == 'datetimerange'){
                if(this.formatType){
                    this.startTime = this.times&&this.times.length ? new Date(this.times[0]).getTime() : '';
                    this.endTime = this.times&&this.times.length ? new Date(this.times[1]).getTime() : '';
                }else{
                    this.startTime = this.times&&this.times.length ? parseTime(this.times[0]) : '';
                    this.endTime = this.times&&this.times.length ? parseTime(this.times[1]) : '';
                }
                
            }else{
                if(this.formatType){
                    this.startTime = this.times&&this.times.length ? new Date((this.times[0])).getTime() : '';
                    this.endTime = this.times&&this.times.length ? new Date(this.times[1]).getTime() : '';
                }else{
                    this.startTime = this.times&&this.times.length ? parseTime(this.times[0]) : '';
                    this.endTime = this.times&&this.times.length ? parseTime(this.times[1]) : '';
                }
            }
            this.$emit("selectTime", {
                startTime: this.startTime,
                endTime: this.endTime,
            });
        },
        resetTimes() {
            this.times = [];
            this.getTimes();
        }
    }
}
</script>
<style lang="less" scoped>

</style>


