<template>
    <div>
       <div class="box null-box" >
            <div class="toolBar">
                <el-row>
                <el-col :span="2"><div class="grid-content " v-show="search"> <div class="left">
                <el-select v-model="selectObj.value"  placeholder="全部渠道"  @change="selectVal">
                    <el-option
                    v-for="item in selectObj.options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </div></div></el-col>
            <el-col :span="24"><div class="grid-content"> <div class="right"> 
                <el-button-group>
                <el-button @click="yesterdayClick">昨日</el-button>
                <el-button @click="lastWeekClick">近7日</el-button>
                <el-button @click="lastMonthClick">近30日</el-button>
            </el-button-group>
            <div class="block">
               <el-date-picker
                v-model="value1"
                @change="selectDate"
                type="daterange"
                range-separator="~"
                start-placeholder="开始日期"
                end-placeholder="结束日期">
                </el-date-picker>
            </div>
            </div></div></el-col>
                </el-row>
       </div>
        </div>
    </div>
</template>

<script>
    export default {
       props: ['search'],
        data() {
            return {
            value1: '',
             selectObj :{
                options: [{
                    value: 'ios',
                    label: 'IOS'
                    }, {
                    value: 'android',
                    label: 'Android'
                    }, {
                    value: 'win',
                    label: 'PC'
                    }, {
                    value: 'websit',
                    label: 'WEB'
                }],
                value: ''
            },
            };
        },
        mounted() {
            this.lastWeekClick();
        },
        methods:{
         selectDate(val){
            let time = [this.format(val[0]),this.format(val[1])];
            this.$emit('func',time);
         },
          selectVal(val){
          this.$emit('selectVal',val);
          },
          format(date) {
            let str = new Date(date);
            let YY = str.getFullYear() + '-';
            let MM = (str.getMonth() + 1 < 10 ? '0' + (str.getMonth() + 1) : str.getMonth() + 1) + '-';
            let DD = (str.getDate() < 10 ? '0' + (str.getDate()) : str.getDate());
            return YY + MM + DD;
      },
      /**
       * 昨天
       */
      yesterdayClick(){
        const end = new Date();
        const start = new Date();
        const yesterday =  start.setTime(start.getTime() - 3600 * 1000 * 24);
        this.value1 = [this.format(yesterday),this.format(end.getTime())];
        this.$emit('func',this.value1);
      },
      /**
       * 上周
       */
      lastWeekClick(){
        const end = new Date();
        const start = new Date();
        const lastWeek =  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
        this.value1 = [this.format(lastWeek),this.format(end.getTime())];
        this.$emit('func',this.value1);
      },
      /**
       *上月
       */
      lastMonthClick(){
        const end = new Date();
        const start = new Date();
        const lastMonth =  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        this.value1 = [this.format(lastMonth),this.format(end.getTime())];
        this.$emit('func',this.value1);
      },
        }
    }
</script>
<style lang="less" scoped>
 @import "../index.less";
</style>


