<template>
	<div>
		<el-pagination  v-if="isTool"
			@size-change="handleSizeChange"
			@current-change="handleCurrentChange"
			:current-page="currentPage"
			:page-size="pageSize"
			:page-sizes="[10, 20, 50, 100]"
			:pager-count="5"
			background
			layout="total, sizes, prev, pager, next, jumper"
			:total="total"
		></el-pagination>
		<el-pagination v-else
			@size-change="handleSizeChange"
			@current-change="handleCurrentChange"
			:current-page="currentPage"
			:page-sizes="[10, 20, 50, 100]"
			:page-size="6"
			:pager-count="5"
			background
			layout="total,prev, pager, next"
			:total="total"
		></el-pagination>
	</div>
</template>

<script>
export default {
	name: 'pagination',
	data() {
		return {
		};
	},
	props: {
		total: {
			type: Number,
			default: 0,
		},
		currentPage: {
			type: Number,
			default: '',
		},
		pageSize: {
			type: Number,
			default: 10,
		},
		isTool: {
			default: true,
		},
	},
	mounted() {},
	methods: {
		handleSizeChange(val) {
			this.$emit('handleSizeChange', val);
		},
		handleCurrentChange(val) {
			this.$emit('handleCurrentChange', val);
		},
	},
};
</script>
<style lang="less" scoped>
</style>



